
















































































































































.note-title {

}
.note-description {
  padding: 1rem;
  background: #bdbdbd;
}
.note-content {
  margin-top: 1rem;
}
.note-container {
  position: fixed;
  top: 4rem;
  bottom: 0;
  right: 0;
  background: #424242;
  padding: 1rem;
  box-sizing: border-box;
  z-index: 4;
}
@media screen and (max-width: 1024px) {
  .note-container {
    top: 0;
  }
}
.note-content-editor {
  position: absolute;
  bottom: 0;
  top: 10rem;
  margin: 0.5rem;
  width: calc(100% - 3rem);
  box-sizing: border-box;
}
.note-content-editor textarea {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  font-family: "Roboto Regular";
}
.note-action {
  width: 100%;
  padding: 0 1rem;
  margin: -1rem;
  margin-bottom: 0;
}
.note-action a {
  display: inline-block;
  height: 3rem;
  line-height: 3rem;
  padding: 0.5rem 1rem;
  box-sizing: border-box;
  position: relative;
}
.note-action a i {
  color: #fff;
  font-size: 1.5rem;
}
.note {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}
.note i {
  font-size: 1.5rem;
  width: 1.5rem;
  height: 1.5rem;
}



